.BuildControls {
    width: 100%;
    background-color: white;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    -ms-flex-align: center;
        align-items: center;
    -webkit-box-shadow: 0 2px 1px #898888;
            box-shadow: 0 2px 1px #898888;
    margin: auto;
    padding: 10px 0;
}

.OrderButton {
    background-color: #494949;
    outline: none;
    cursor: pointer;
    color: white;
    font-family: inherit;
    font-size: 1.2em;
    padding: 15px 30px;
}

.OrderButton:hover, .OrderButton:active {
    background-color: black;
    color: white;
}

.OrderButton:disabled {
    background-color: #C7C6C6;
    cursor: not-allowed;
    border: 1px solid #ccc;
    color: #888888;
}

.OrderButton:not(:disabled) {
    -webkit-animation: enable 0.3s linear;
            animation: enable 0.3s linear;
}


